<template>
  <div class="login">
    <!---->
    <logicheader v-bind:title="msg"></logicheader>
    <!---->
    <div class="logintop">
      <input v-model="username" class="username" type="text" placeholder="">
      <input v-model="password" class="password" type="password" placeholder="">
      <van-switch v-model="checked" size="22px"/>
      <input v-model="captcha_code" class="code" type="text" placeholder="">
      <div class="codebtn">
        <img :src=this.imgsrc alt="">
        <span></span>
        <button @click="nextimage" class="nextimg"></button>
      </div>
    </div>
    <!---->
    <div class="loginbuttom">
      <p>
        
      </p>
      <p>
        
      </p>
      <button @click="submitbtn" class="loginbtn"></button>
      <!---->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="kuang" v-if="show">
          <van-icon name="warning-o" class="van1"/>
          <p v-model="kuangmsg">{{kuangmsg}}</p>
          <!--<p>//</p>-->
          <button @click="confirm"></button>
        </div>
      </transition>
      <router-link :to="{path:'/forgetpage',query:''}">?</router-link>
    </div>

  </div>
</template>

<script>
  // js
  import Logicheader from "./logicheader";
  import Vue from 'vue'

  export default {
    name: "loginpage",
    components: {Logicheader},
    data() {
      return {
        checked: false,
        msg: "",
        imgsrc: "",
        username: "",
        password: "",
        captcha_code: "",
        show: false,
        kuangmsg: "",
        allinfor: "",
        isrightopc: true
        ,
      }
    },
    methods: {
      confirm() {
        this.show = false;
      },
      // onInput(checked) {
      //   Dialog.confirm({
      //     title: '',
      //     message: ''
      //   }).then(() => {
      //     this.checked = checked;
      //   });
      // },


      // 
      nextimage() {
        Vue.axios.post("/v1/captchas", {}).then((result) => {
          this.imgsrc = result.data.code
        })
        console.log(this.imgsrc);
      },

      // ,
      submitbtn() {
        // 
        Vue.axios.post("/v2/login", {
          username: this.username,
          password: this.password,
          captcha_code: this.captcha_code
        }).then((res) => {
          console.log(res.data);
          if (res.data.message == "") {
            this.show = true;
            this.kuangmsg = "";
            this.nextimage();
          } else if (res.data.message == "") {
            this.show = true;
            this.kuangmsg = ""
            this.nextimage();
          } else if (res.data.message == "") {
            this.show = true;
            this.kuangmsg = ""
            this.nextimage();
          } else if (res.data.message == "") {
            this.show = true;
            this.kuangmsg = "//"
            this.nextimage();
          } else if (res.data.message == "") {
            this.show = true;
            this.kuangmsg = ""
            this.nextimage();
          } else {
            // 
            this.$router.push({path: "/index"});
            // vuex
            this.$store.commit('saveisrightopc',this.isrightopc)
            // // localstorage
            // localStorage.setItem('localisrightopc', JSON.stringify(this.isrightopc))

            this.allinfor = res.data
            // vuex
            this.$store.commit('saveUserStatus', this.allinfor);
            // // localstorage
            // localStorage.setItem('localdata', JSON.stringify(this.allinfor))
          }
        })
      },
    },

    mounted() {
      // // 
      // this.fromindex = this.$route.query.msg;
      // 
      this.nextimage();

    },

  }
</script>


<style scoped lang="less">
  /*css*/
  .login {
    width: 100%;
    .logintop {
      display: inline-block;
      position: relative;
      margin-top: 1.05rem;
      input {
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.46rem;
        padding: 0.2rem;
        border-bottom: 1px solid #f1f1f1;
      }
      .codebtn {
        position: absolute;
        bottom: 0rem;
        right: 0.2rem;
        img {

        }
        span {
          font-size: 0.2rem;
          position: absolute;
          right: 0;
          top: 0.1rem;
          margin-left: 0.5rem;
        }
        .nextimg {
          background: transparent;
          font-size: 0.2rem;
          color: #2d65ff;
          margin: 0 0 0 0.1rem;
          padding-bottom: 0.1rem;
        }
      }
      .van-switch {
        position: absolute;
        top: 1.2rem;
        right: 0.2rem;
        height: 0.2rem;
        width: 0.7rem;
        background: #d0d0d0;
        .van-switch__node {
          position: absolute;
          top: -0.12rem;
          background: #eeeeee;
          width: .4rem;
          height: .4rem;
        }
      }
      .van-switch--on {
        background: #37e254;
        position: absolute;
        top: 1.2rem;
        height: 0.2rem;
        width: 0.7rem;
        .van-switch__node {
          position: absolute;
          top: -0.12rem;
          background: #eeeeee;
          width: .4rem;
          height: .4rem;

        }
      }
    }
    .loginbuttom {
      padding: 0.2rem 0 0 0.2rem;
      overflow: hidden;
      p {
        font-size: 0.2rem;
        padding: 0.01rem 0.01rem 0.25rem 0;
        color: red;
        &:nth-child(2) {
          padding-bottom: 0.2rem;
        }
      }
      .loginbtn {
        width: 96%;
        margin: 0 0 0.2rem;
        font-size: .3rem;
        color: #fff;
        background-color: #4cd964;
        padding: .24rem 0;
        border-radius: .08rem;
        text-align: center;
      }
      /**/
      .kuang {
        width: 74%;
        background-color: #fff;
        text-align: center;
        position: absolute;
        left: 13%;
        top: 2.8rem;
        border-radius: .25rem .25rem 0 0;
        .van1 {
          padding-top: .2rem;
          font-size: 1.5rem;
          color: #f8cb86;
        }
        p {
          font-size: .3rem;
          color: #333;
          line-height: .4rem;
          margin-top: .3rem;
          padding-bottom: .3rem;
        }
        button {
          font-size: .4rem;
          color: #fff;
          font-weight: 700;
          margin-top: .2rem;
          background-color: #4cd964;
          width: 100%;
          line-height: .9rem;
          border-radius: 0 0 .25rem .25rem;
        }
      }

      a {
        font-size: 0.25rem;
        color: #30b1ff;
        margin: 0.2rem 0.5rem 0 0;
        float: right;
      }
    }

  }


</style>



