<template>
    <div class="mine">
      <my-header></my-header>
      <div class="top">
        <input type="text" placeholder="" v-model="input1">
        <input type="text" placeholder="" v-model="input2">
        <input type="text" placeholder="" v-model="input3">
        <input type="text" placeholder="" v-model="input4">
        <input type="text" placeholder="" v-model="input5">
        <!---->
        <div class="right">
          <img :src="img" alt="">
          <p @click="getimg"></p>
          <span class="span1" @click="getimg"></span>
        </div>
        <!---->
        <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
          <div class="kuang" v-if="show">
            <van-icon name="warning-o"  class="van1"/>
            <p>{{words}}</p>
            <button  @click="confirm"></button>
          </div>
        </transition>
      </div>
      <button class="btn1" @click="changepassword"></button>
    </div>
</template>

<script>
  import Vue from "vue"
  import MyHeader from "./myHeader";
    export default {
        name: "changepassword",
      components:{MyHeader},
      data(){
        return{
          show:false,
          img:"",
          input1:"",
          input2:"",
          input3:"",
          input4:"",
          input5:"",
          words:"",
          input1Value:"",
        }
      },
      methods:{
          confirm(){
            this.show=false;
          },
        getimg(){
          //  
          Vue.axios.post("/v1/captchas").then((result)=>{
            // console.log(result.data);
            this.img=result.data.code;
          }).catch((error)=>{
            console.log(error);
          })
        },
        changepassword(){
          //  
          Vue.axios.post("v2/changepassword",{
            username:this.input1,
            oldpassWord:this.input2,
            newpassword:this.input3,
            confirmpassword:this.input4,
            captcha_code :this.input5,
          }).then((result)=>{
            // console.log(result.data);
            if(result.data.message==""){
                this.show=true;
                this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words=""
            }else if(result.data.message==""){
              this.show=true;
              this.words="";
            }else if(result.data.message==""){
              this.show=true;
              this.words="";
            }else{
              this.show=true;
              this.words="";
            }
          }).catch((error)=>{
            console.log(error);
          });
        },
      },
      created() {
        // 
        this.getimg();
      }
    }
</script>

<style scoped lang="less">
.top {
  margin-top: 1.3rem;
  width: 100%;
  background-color: white;
  display: inline-block;
  position: relative;
  input {
    display: block;
    width: 100%;
    border-bottom: 1px solid #f1f1f1;
    font-size: .3rem;
    line-height: 1rem;
    color: #666;
    padding-left: .3rem;
  }

  .right {
    position: absolute;
    bottom: .2rem;
    right: .4rem;
    font-size: .2rem;
    .span1 {
      color: #3b95e9;
    }
    p{
      color: #666;
      padding: .2rem 0;
    }
    img{
      position: absolute;
      right: 1rem;
      bottom: -.1rem;
      width: 1.5rem;
      height: 1rem;
    }
  }
  .kuang{
    width: 74%;
    background-color:#fff;
    text-align: center;
    position: absolute;
    left: 13%;
    top: 2.5rem;
    border-radius:.25rem .25rem 0 0 ;
    .van1{
      padding-top: .4rem;
      font-size: 1.5rem;
      color: #f8cb86;
    }
    p{
      font-size: .3rem;
      color: #333;
      line-height: .4rem;
      margin-top: .3rem;
      padding-bottom: .3rem;
    }
    button{
      font-size: .4rem;
      color: #fff;
      font-weight: 700;
      margin-top: .4rem;
      background-color: #4cd964;
      width: 100%;
      line-height: .9rem;
      border-radius: 0 0 .25rem .25rem;
    }
  }
}
  .btn1{
    width: 96%;
    margin:.5rem  2%;
    font-size: .3rem;
    color: #fff;
    background-color: #4cd964;
    padding: .3rem 0;
    border-radius: .1rem;
    text-align: center;
  }
</style>



