<template>
  <!---->
  <div class="indexapp">
    <indexheader></indexheader>
    <!---->
    <ul class="ultop">
      <li class="litop clear">
        <span class="left">:</span>
        <span class="right">,</span>
      </li>

      <li @click="gologiccity(guessCity)" class="libottom clear">
        <span class="left">{{guessCity.name}}</span>
        <van-icon name="arrow" class="right"></van-icon>
        <!--<span class="right">></span>-->
      </li>
    </ul>
    <!---->
    <ul class="ulmiddle">
      <li class="li_m_t">
        <span></span>
      </li>
      <!---->
      <li class="li_m_m">
        <a v-for="h in hotCity" @click="gologiccity(h)">{{h.name}}</a>
      </li>
    </ul>
    <!---->
    <div class="ulbottom">
      <!---->
      <ul class="li_b_t" v-for="(Obj,k) in sortObjByKey(groupCity)">
        <span>{{k}}</span>
        <li class="li_b">
          <!---->
          <a v-for="(v,key) in Obj" @click="gologiccity(v)">{{v.name}}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  //http
  import {get} from '../config/http'
  import Indexheader from "./indexheader";

  export default {
    name: "index",
    components: {Indexheader},
    // 
    data() {
      return {
        guessCity: "",//
        hotCity: [],//
        groupCity: [],//
        rightopc:""

      }
    },
    // 
    methods: {
      // 
      sortObjByKey(groupCity) {
        let keys = Object.keys(groupCity).sort();
        let newObj = {};
        for (let i = 0; i < keys.length; i++) {
          let index = keys[i];
          newObj[index] = groupCity[index];
        }
        return newObj
      },
      //
      gologiccity(v) {
        this.$router.push({path: '/citypage', query: {city: v.name, cityid: v.id}})
      }
    },
    // 
    mounted() {
      // get
      get('/v1/cities', {
        type: 'guess'
      }).then((guessCity) => {
        this.guessCity = guessCity
      }).catch((error) => {
        console.log(error);
      })
      // get
      get('/v1/cities', {
        type: 'hot'
      }).then((hotCity) => {
        this.hotCity = hotCity;
      }).catch((error) => {
        console.log(error);
      })
      // get
      get('/v1/cities', {
        type: 'group'
      }).then((groupCity) => {
        this.groupCity = groupCity;
      }).catch((error) => {
        console.log(error);
      })
      //vuexisrightopc
      // let isrightopc = this.$store.state.isrightopc;
      // if (isrightopc) { // vuex
      //   this.rightopc = isrightopc;
      // } else { //vuex localstorage
      //   let localisrightopc = JSON.parse(localStorage.getItem('localisrightopc'));
      //   if (localisrightopc) {
      //     this.rightopc = localisrightopc;
      //   }
      //   else { // localstorage
      //     // 
      //     this.isrightopc = true
      //     console.log('');
      //   }
      // }
    }


  };
</script>

<style scoped lang="less">
  /**/
  .indexapp {
    /**/
    .ultop {
      margin-top: 1rem;
      line-height: 0.8rem;
      font-size: 0.2rem;
      .litop {
        border-bottom: 0.2px solid #dedede;
        padding: 0 0.2rem;
        .right {
          color: #8b8b8b;
        }
      }
      .libottom {
        border-bottom: 1.7px solid #d0d0d0;
        padding: 0 0.2rem;
        height: 0.7rem;
        line-height: 0.7rem;
        .left {
          color: #2d65ff;
          font-size: 0.3rem;
        }
        .right {
          font-size: 0.3rem;
          padding-top: 0.2rem;
          color: #999;
        }
      }
    }
    /**/
    .ulmiddle {
      margin-top: 0.19rem;
      /*line-height: 0.4rem;*/
      font-size: 0.2rem;
      border-top: 1.7px solid #d0d0d0;
      .li_m_t {
        line-height: 0.55rem;
        border-bottom: 0.2px solid #dedede;
        span {
          padding: 0 0.2rem;
        }
      }
      .li_m_m {
        font-size: 0;
        /*line-height: 0.8rem;*/
        border-bottom: 0.2px solid #dedede;
        /*padding: 0.4rem;*/
        a {
          margin: 0;
          line-height: 0.7rem;
          text-align: center;
          color: #4993ff;
          font-size: 0.25rem;
          display: inline-block;
          width: 25%;
          border-bottom: 0.2px solid #dedede;
          border-right: 0.2px solid #dedede;
          &:nth-child(4) {
            border-right: 0;
          }
          &:nth-child(8) {
            border-right: 0;
          }
        }
      }
    }
    /**/
    .ulbottom {
      margin-top: 0.2rem;
      /*line-height: 0.4rem;*/
      font-size: 0.22rem;
      border-top: 1.7px solid #d0d0d0;
      .li_b_t {
        line-height: 0.58rem;
        border-bottom: 0.2px solid #dedede;
        span {
          padding: 0 0.2rem;
          &:nth-child(2) {
            padding: 0;
            font-size: 0.5rem;
            color: #848484;
          }
        }
      }
      .li_b {
        font-size: 0;
        line-height: 0;
        border-bottom: 0.2px solid #dedede;
        a {
          margin: 0;
          text-align: center;
          color: #4993ff;
          font-size: 0.25rem;
          display: inline-block;
          width: 25%;
          line-height: 0.7rem;
          padding: 0 0.02rem;
          border: 0.2px solid #dedede;
          border-left: 0;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: black;

        }
      }
    }
  }

</style>



