<template>
  <div class="citypage">
    <!---->
    <cityheader v-bind:citypage="city"></cityheader>
    <!---->
    <div class="locatinput">
      <input v-model="inputval" type="text" placeholder="">
      <!---->
      <i class="iconfont deletebtn" @click="deketeinputval" :style="{opacity:isdeleBtnOpc}">&#xe604;</i>
      <!---->
      <button @click="submitmsg(inputval)" class="loginbtn"></button>
    </div>
    <!---->
    <div class="houstory" :style="{opacity:isHoustoryOpc}"></div>
    <!---->
    <div class="houstoryshow">
      <li v-for="c in searchcityres" @click="towaimaipage(c)">
        <h4>{{c.name}}</h4>
        <p>{{c.address}}</p>
      </li>
    </div>


  </div>
</template>

<script>

  import Cityheader from "./cityheader";
  import Vue from "vue"

  export default {
    name: "citypage",
    components: {Cityheader},
    data() {
      return {
        inputval: "",//
        isdeleBtnOpc: 0,//
        isHoustoryOpc: 1,//
        city: "",//
        cityid: "",//id
        searchcityres: "",//
        geohash: {},//
        shopinfor: {},//`
        latitude:"",//
        longitude:"",//
      }
    },
    methods: {
      // 
      submitmsg(inputval) {
        // 
        this.isHoustoryOpc = 0;
        // 
        if (inputval.length !== 0) {
          // get()
          Vue.axios.get("/v1/pois", {
            params: {
              keyword: this.inputval,
              city_id: this.cityid,
            }
          }).then((result) => {
            this.searchcityres = result.data

          }).catch((error) => {
            console.log(error);
          })
        }
      },
      // 
      towaimaipage(v) {
        this.$router.push({path:"/waimaipage"})

        // geohashvuex
        this.$store.commit('savelocalgeohash',v.geohash)

        // latitudevuex
        this.$store.commit('savelacalshoplatitude',v.latitude)

        // longitudevuex
        this.$store.commit('savelacalshoplongitude',v.longitude)




      },
      // 
      deketeinputval() {
        this.inputval = ""
        this.isdeleBtnOpc = 0
      }
    },
    // 
    watch: {
      inputval(inputval) {
        if (inputval.length === 0) {
          this.isdeleBtnOpc = 0
        } else {
          this.isdeleBtnOpc = 1;
        }
      }
    },
    // 
    created() {
      // index
      this.city = this.$route.query.city;
      this.cityid = this.$route.query.cityid;
    },
  }
</script>

<style scoped lang="less">
  /**/
  .locatinput {
    display: inline-block;
    position: relative;
    width: 100%;
    margin-top: 1rem;
    padding: 0.2rem;
    background: white;
    input {
      width: 100%;
      height: 0.6rem;
      border: 1px solid #e4e4e4;
      padding: 0 .1rem;
      font-size: .24rem;
      margin-bottom: 0.2rem;
      color: #333
    }
    .deletebtn {
      font-size: 0.35rem;
      font-weight: bold;
      color: #4a6fff;
      position: absolute;
      top: 0.38rem;
      right: 0.4rem;
      background: transparent;
      opacity: 0;

    }
    .loginbtn {
      width: 100%;
      height: 0.6rem;
      background-color: #3190e8;
      font-size: .3rem;
      border-radius: 0.04rem;
      color: #fff;
    }
  }

  /**/
  .houstory {
    font-size: .21rem;
    padding: 0.08rem 0.2rem 0.08rem 0.2rem;
    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;
  }

  /**/
  .houstoryshow {
    li {
      margin: 0 auto;
      padding-top: .05rem;
      border-bottom: 1px solid #e4e4e4;
      h4 {
        margin: 0.2rem 0.35rem;
        width: 90%;
        font-size: .3rem;
        color: #333;
      }
      p {
        width: 90%;
        margin: 0 auto .25rem;
        font-size: .2rem;
        color: #999;
      }
    }
  }

</style>



