<template>
    <div class="mine">
      <!---->
      <div class="top clearfix">
        <van-icon name="arrow-left" class="left"/>
        <span></span>
        <van-icon name="contact" class="right"/>
      </div>
      <!---->
      <div class="location clearfix">
        <router-link :to="{path:'/chooseaddress',query:{name:''}}">
          <van-icon name="location-o" class="left"/>
          <span></span>
          <van-icon name="arrow" class="right"/>
        </router-link>
      </div>
      <!---->
      <div class="time clearfix">
        <span class="right"> |  6:45</span>
        <p></p>
        <span class="right right1"></span>
      </div>
      <!---->
      <div class="pay">
        <div class="methods clearfix">
          <span class="left"></span>
          <!---->
          <button  @click="showPopup" class="right">
            
            <van-icon name="arrow" class="btn"/>
          </button>
        </div>
        <!---->
        <van-popup v-model="show" position="bottom" :style="{height:'40%'}">
          <div class="show clearfix">
            <p></p>
            <span class="left"> ()</span>
            <van-icon name="checked" class="right"/>
            <h4 class="clearfix">
              <span class="left"></span>
              <van-icon name="checked" class="right" @click="deleted"/>
            </h4>
          </div>
        </van-popup>
        <!---->
        <div class="money clearfix">
          <span class="left"></span>
          <span class="right">APP</span>
        </div>
      </div>
      <!---->
      <div class="middle">
        <p>
          <img src="//elm.cangdu.org/img/16c40699fae50011.jpg" alt="">
          mark1111333
        </p>
        <!--test-->
        <div class="test">
          <p class="clearfix">
            <span class="left">test</span>
            <span class="span1">x 1</span>
            <span class="right">30</span>
          </p>
          <!---->
          <p class="clearfix">
            <span class="left"></span>
            <span class="right">30708.5</span>
          </p>
          <!---->
          <p class="clearfix">
            <span class="left"></span>
            <span class="right">4</span>
          </p>
          <!---->
          <p class="clearfix">
            <span class="left"> 30742.5</span>
            <span class="span1 span2"></span>
          </p>
          <!---->
          <p>
            <span class="span1 span3">30742.5</span>
          </p>

        </div>
      </div>
      <!---->
      <div class="sign">
        <router-link :to="{}" class="clearfix">
          <span class="left"></span>
          <div class="right">
            <span></span>
            <van-icon name="arrow" class="van"/>
          </div>
        </router-link>
        <!---->
        <router-link :to="{}" class="clearfix">
          <span class="left"></span>
          <div class="right">
            <span></span>
            <van-icon name="arrow" class="van"/>
          </div>
        </router-link>
      </div>
      <!---->
      <div class="black">
        <span class="left"> 30874.5</span>
        <span class="right"></span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "confirmorder",
      data(){
          return{
            show:false,
          }
      },
      methods:{
        showPopup() {
          this.show = true;
        },
        deleted(){
          this.show =false;
        }
      }
    }
</script>

<style scoped lang="less">
.mine{
  background-color: #f2f2f2;
  .top{
    background-color: #3190e8;
    padding: .25rem .3rem .1rem .2rem;
  .left{
  color: white;
  font-size: .5rem;
   }
    span{
      color: white;
      font-size: .35rem;
      font-weight: 700;
      margin-left:2.3rem;
    }
    .right{
      color: white;
      font-size: .4rem;
    }
  }
  .location{
    background:url(data:image/png;base64,) 0 100% repeat-x;
    background-size: .4rem;
    background-color: white;
    padding: .5rem .2rem;
    .left{
      color: #3190e8;
      font-size: .45rem;
      padding-right: .1rem;
    }
    .right{
      color: #999;
    }
  }
  .time{
    border-left: .1rem solid #3190e8;
    margin-top: .2rem;
    background-color: white;
    padding: .3rem;
    p{
      font-size: .4rem;
      font-weight: 700;
      padding: .3rem 0 0 .2rem;
    }
    .right{
      color: #3190e8;
    }
    .right1{
      color: white;
      font-size: .2rem;
      background-color: #3190e8;
      padding: .1rem;
      border-radius: .1rem;
    }
  }
  .pay{
    margin-top: .2rem;
    background-color: white;
    .methods{
      span{
        padding: .25rem;
      }
      .right{
        background-color: white;
        padding: .3rem;
        font-size: .25rem;
        color: #aaa;
        .btn{
          color: #aaa;
        }
      }
    }

    .show{
      p{
        font-size:.35rem ;
        text-align: center;
        padding: .3rem;
      }
      span{
        color: #ccc;
      }
      .right{
        font-size: .4rem;
        color: #ddd;
      }
      h4{
        .left{
          color: #333;
          padding-top: .5rem;
        }
        .right{
          margin-left: 4rem;
          color: #4cd964;
        }
      }
    }
    .money{
      border-top: .01rem solid #f5f5f5;
      padding: .2rem  .2rem .3rem;
    }
  }
  .middle{
    margin-top: .2rem;
    background-color: white;
    p{
      font-size: .4rem;
      border-bottom: .01rem solid #f5f5f5;
      img{
        padding: .3rem 0 .3rem .3rem;
        width: .6rem;
        height: .6rem;
        vertical-align: -.5rem;
      }
    }
    .test{
      p{
        padding: .2rem .2rem .2rem .3rem;
        border-bottom: none;
        span{
          color: #666;
          font-size: .3rem;
        }
        .span1{
          color: #f60;
          margin-left: 3.6rem;
          vertical-align: .1rem;
        }
        .span2{
          margin-left: 2.8rem;
        }
        .span3{
          margin-left: 4.8rem;
        }
      }
    }
  }
  .sign{
    background-color: white;
    margin-top: .2rem;
    a{
      display: block;
      padding: .2rem;
      .right{
        span,.van{
          color: #aaa;
        }

      }
    }
  }
  .black{
    margin-top: .2rem;
    span{
      padding: .3rem;
    }
    .left{
      width: 70%;
      background-color: black;
      color: white;
    }
    .right{
      width: 30%;
      background-color: #56d176;
      color: white;
     text-align: center;

    }
  }
}
</style>



